.block3 {
  background-image: url(../img/center.jpg);
  background-position: center;
  height: 500px;
  width: 100%;
  padding: 20px;
  color: white;
}

img.img_block3 {
	width: 400px;
	height: 320px;
}

button.btm_resume {
  display: inline-block;
  color: black;
  font-size: 125%;
  font-weight: 700;
  text-decoration: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: .25em .5em;
  outline: none;
  border: 1px solid rgb(250,172,17);
  border-radius: 7px;
  background: rgb(255,212,3) -webkit-gradient(linear, left top, left bottom, from(rgb(255,212,3)), to(rgb(248,157,23)));
  background: rgb(255,212,3) -webkit-linear-gradient(rgb(255,212,3), rgb(248,157,23));
  background: rgb(255,212,3) -o-linear-gradient(rgb(255,212,3), rgb(248,157,23));
  background: rgb(255,212,3) linear-gradient(rgb(255,212,3), rgb(248,157,23));
  -webkit-box-shadow: inset 0 -2px 1px rgba(0,0,0,0), inset 0 1px 2px rgba(0,0,0,0), inset 0 0 0 60px rgba(255,255,0,0);
          box-shadow: inset 0 -2px 1px rgba(0,0,0,0), inset 0 1px 2px rgba(0,0,0,0), inset 0 0 0 60px rgba(255,255,0,0);
  -webkit-transition: border-color .2s, -webkit-box-shadow .2s;
  transition: border-color .2s, -webkit-box-shadow .2s;
  -o-transition: box-shadow .2s, border-color .2s;
  transition: box-shadow .2s, border-color .2s;
  transition: box-shadow .2s, border-color .2s, -webkit-box-shadow .2s;
} 
button.btm_resume:hover {
  -webkit-box-shadow: inset 0 -1px 1px rgba(0,0,0,0), inset 0 1px 2px rgba(0,0,0,0), inset 0 0 0 60px rgba(255,255,0,.5);
          box-shadow: inset 0 -1px 1px rgba(0,0,0,0), inset 0 1px 2px rgba(0,0,0,0), inset 0 0 0 60px rgba(255,255,0,.5);
}
button.btm_resume:active {
  padding: calc(.25em + 1px) .5em calc(.25em - 1px);
  border-color: rgba(177,159,0,1);
  -webkit-box-shadow: inset 0 -1px 1px rgba(0,0,0,.1), inset 0 1px 2px rgba(0,0,0,.3), inset 0 0 0 60px rgba(255,255,0,.45);
          box-shadow: inset 0 -1px 1px rgba(0,0,0,.1), inset 0 1px 2px rgba(0,0,0,.3), inset 0 0 0 60px rgba(255,255,0,.45);
}


