.block4 {
  background-image: url(../img/center.jpg);
  background-position: center;
  height: 500px;
  padding: 20px;
  color: white;
}

li {
	list-style: none;
}
button.btr-work {
  position: relative;
  display: inline-block;
  width: 6em;
  height: 2.5em;
  line-height: 2.4em;
  vertical-align: middle;
  text-align: center;
  text-decoration: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #000;
  outline: none;
  border: 1px solid rgba(110,121,128,.8);
  border-top-color: rgba(0,0,0,.3);
  border-radius: 5px;
  background: rgb(206, 220, 231) -webkit-gradient(linear, left top, left bottom, from(rgb(206,220,231)), to(rgb(89,106,114)));
  background: rgb(206, 220, 231) -webkit-linear-gradient(rgb(206,220,231), rgb(89,106,114));
  background: rgb(206, 220, 231) -o-linear-gradient(rgb(206,220,231), rgb(89,106,114));
  background: rgb(206, 220, 231) linear-gradient(rgb(206,220,231), rgb(89,106,114));
  -webkit-box-shadow:
   0 -1px rgba(10,21,28,.9) inset,
   0 1px rgba(255,255,255,.5) inset;
          box-shadow:
   0 -1px rgba(10,21,28,.9) inset,
   0 1px rgba(255,255,255,.5) inset;
}
button.btr-work:hover {
  background: -webkit-gradient(linear, left top, left bottom, from(#d2dfea), to(#71828c));
  background: -webkit-linear-gradient(#d2dfea, #71828c);
  background: -o-linear-gradient(#d2dfea, #71828c);
  background: linear-gradient(#d2dfea, #71828c);
}
button.btr-work:active {
  line-height: 2.6em;
  background: -webkit-gradient(linear, left top, left bottom, from(#bac6cf), color-stop(20%, #c5d3de), to(#71828c));
  background: -webkit-linear-gradient(#bac6cf, #c5d3de 20%, #71828c);
  background: -o-linear-gradient(#bac6cf, #c5d3de 20%, #71828c);
  background: linear-gradient(#bac6cf, #c5d3de 20%, #71828c);
  -webkit-box-shadow: 0 -1px rgba(255,255,255,.4) inset;
          box-shadow: 0 -1px rgba(255,255,255,.4) inset;
}
button.btr-work:before {
  content: "";
  position: absolute;
  top: -10px; right: -10px; bottom: -10px; left: -10px;
  z-index: -1;
  border-radius: 8px;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(200,200,200,.5)), to(rgba(240,240,240,.5)));
  background: -webkit-linear-gradient(rgba(200,200,200,.5), rgba(240,240,240,.5));
  background: -o-linear-gradient(rgba(200,200,200,.5), rgba(240,240,240,.5));
  background: linear-gradient(rgba(200,200,200,.5), rgba(240,240,240,.5));
}


